* {
	box-sizing: border-box;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	list-style: none;
}

body {
	margin: 0;
	height: 100vh;
	width: 100wh;
}
ul {
	display: inline-block;
}
li {
	background-color: #cacaca;
	margin-right: 20px;
	margin-left: 600px;
	padding: 10px;
	margin-bottom: 10px;
}
h1 {
	margin-left: 20px;
	margin-top: 50px;
	color: mediumvioletred;
}
h2 {
	margin-top: -10px;
	margin-left: 20px;
}
form {
	margin-left: 20px;
}
.input {
	height: 30px;
	width: 300px;
	border: 0;
	border-bottom: 1px solid mediumvioletred;
}
.button {
	height: 30px;
	color: white;
	background-color: mediumvioletred;
	border: 0;
	
}

.favourite {
	background-color: mediumvioletred;
	color: white;
}
.nofavourite {
	background-color: #cacaca;
}


